import React from "react";
import ReactDom from "react-dom";
import ImageGallery from "react-image-gallery";
import CloseBtn from './closeBtn'
import "react-image-gallery/styles/css/image-gallery.css";
import { motion } from "framer-motion";

import ProjectInfo from "./projectInfo";


const ProjectModal = (props) => {
    let projectInfo = props.projectInfo

    const tempImgList = []
    projectInfo.imgs.forEach(img => {
        tempImgList.push({
			original: require(`../image/portfolio/${projectInfo.folder}/${img}`),
			thumbnail: require(`../image/portfolio/${projectInfo.folder}/${img}`),
		});
    });

	const modalVariants = {
		init: {
			opacity: 0,
			y: 500,
		},
		anim: {
			opacity: 1,
			y: 0,
		},
	};

	const clickHideModal = (event) => {
        if(event.target?.id === "modalOverlay") props.hideModal();
	};
	if (props.modalShow) {
		return ReactDom.createPortal(
			<>
				<div
					className='modalOverlay'
					id='modalOverlay'
					onClick={(event) => clickHideModal(event)}>
					<motion.div
						className='modalContainer'
						variants={modalVariants}
						initial='init'
						animate='anim'>
						<div className='modalHeader'>
                            <div className="headerTitle">
                                {projectInfo.projectTitle}
                            </div>
                            <CloseBtn hideModal = {props.hideModal} />
						</div>
						<div className='modalContent'>
                            <div className='contentHeader'>
                                {projectInfo.projectName}
                            </div>
							<ImageGallery 
                            className='modalContent' 
                            items={tempImgList} 
                            lazyLoad={true}
                            showNav={true}
                            showThumbnails={false}
                            />
                            <ProjectInfo projectInfo={projectInfo} showDesc={true}/>
						</div>
					</motion.div>
				</div>
			</>,
			document.getElementById("portal")
		);
	} else {
		return <></>;
	}
};

export default ProjectModal;
