import React from "react";
import { motion } from "framer-motion";
import Footer from "./component/footer";
import "./css/portfolio.min.css";
import ProjectBlock from "./component/projectBlock";
import ProjectInfo from "./component/projectInfo";

import  Youtube  from 'react-lazyload-youtube'
import 'react-lazyload-youtube/dist/index.css'

const Portfolio = () => {
	const fypInfo = {
		projectTitle: "Block-based python learning platform",
		projectName: "Final Year Project",
		projectType: "Web Platform",
		skills: [
			{ name: "React", type: "FE" },
			{ name: "Redux", type: "FE" },
			{ name: "Bootstrap", type: "FE" },
			{ name: "HTML", type: "FE" },
			{ name: "CSS", type: "FE" },
			{ name: "JavaScript", type: "OT" },
			{ name: "Axios", type: "OT" },
			{ name: "JSON Web Token", type: "OT" },
			{ name: "Node.js", type: "BE" },
			{ name: "Express", type: "BE" },
			{ name: "SQL", type: "BE" },
		],
		descr: (
			<p className='contentInfoText'>
				<b>Block-based python learning platform</b> is a STEM education
				platform that allows users to write their own python code by
				drag and drop block. The platform will allow users to export,
				import their project to their own device or save to the server
				after login. Also, the platform provides a lecture note editor
				for the instructor to lecture notes and publish to their own
				lecture group. The development of the project is in progress,
				more feature might be added.
			</p>
		),
	};
	const projectInfos = [
		{
			projectTitle: "VRpedia",
			projectName: "VR Application Development Project",
			projectType: "VR Application",
			folder: "S9",
			mainImg: "img1.jpg",
			skills: [{ name: "Unreal Engine", type: "SW" }],
			imgs: [
				"img1.jpg",
				"img2.png",
				"img3.png",
				"img4.png",
				"img5.png",
				"img6.png",
			],
			descr: (
				<p className='contentInfoText'>
					<b>VRpedia</b> is a VR-type encyclopedia that can help
					people fully understand the object's appearance and
					characteristics. Unlike photos and videos, VRpedia can
					provide students with concepts of simulated phenomena. The
					application can display a huge variety of different 3D
					objects, allowing students to observe them from different
					angles with no restriction.
				</p>
			),
		},
		{
			projectTitle: "NeedConnect",
			projectName: "SDG Asia Innotech Challenge 2020",
			projectType: "Web platform",
			folder: "S7",
			mainImg: "Image 282.png",
			skills: [
				{ name: "React", type: "FE" },
				{ name: "Bootstrap", type: "FE" },
				{ name: "HTML", type: "FE" },
				{ name: "SCSS", type: "FE" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Framer Motion", type: "OT" },
			],
			imgs: [
				"Image 278.png",
				"Image 280.png",
				"Image 281.png",
				"Image 282.png",
			],
			descr: (
				<p className='contentInfoText'>
					<b>NeedConnect</b> is a web application developed in the SDG
					Asia Innotech Challenge 2020 24 hours Hackathon event. The
					Application's aim is to resolve the resource mismatch around
					the world and fulfilling the "Good Health and Well-being"
					sustainable development goal. The application contains a map
					to visualize the need for a different region, allow charity
					groups to host resources delivery events for volunteers to
					join freely, allow the donor to donate the resource to the
					region they want, and track the resource delivery
					transparently.
				</p>
			),
		},
		{
			projectTitle: "Weather app",
			projectName: "Data visualization Project",
			projectType: "Web platform",
			folder: "S8",
			mainImg: "mainImg.png",
			skills: [
				{ name: "HTML", type: "FE" },
				{ name: "SCSS", type: "FE" },
				{ name: "D3.js", type: "OT" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Axios", type: "OT" },
			],
			imgs: [
				"mainImg.png",
				"Image 262.png",
				"Image 263.png",
				"Image 264.png",
				"Image 265.png",
			],
			descr: (
				<p className='contentInfoText'>
					<b>Weather app</b> is a responsive web application that
					provides visualized weather information. The application
					receives data from the Hong Kong Observatory API, providing
					current weather information, weather forecast, weather
					alert, etc.
				</p>
			),
		},
		{
			projectTitle: "大笪地社交平台",
			projectName: "HONG KONG THEI, CARITAS JOINT PROJECT",
			projectType: "Web platform",
			folder: "S2",
			mainImg: "mainImg.png",
			skills: [
				{ name: "Bootstrap", type: "FE" },
				{ name: "HTML", type: "FE" },
				{ name: "CSS", type: "FE" },
				{ name: "JavaScript", type: "OT" },
				{ name: "Jquery", type: "OT" },
				{ name: "PHP", type: "OT" },
				{ name: "SQL", type: "BE" },
			],
			imgs: [
				"mainImg.png",
				"S2img00.png",
				"S2img01.png",
				"S2img02.png",
				"S2img03.png",
			],
			descr: (
				<p className='contentInfoText'>
					<b>大笪地社交平台</b> is an online social media platform
					allowing HKPA members to communicate with each other when
					the HKPA center is closed. The platform allows HKPA staff to
					post the information about their latest activity,
					advertisements for the monthly bazaar event, and update the
					event schedule into the calendar. Members can also write
					posts to share photos and recipes with other members.
				</p>
			),
		},
		{
			projectTitle: "Clone Battle",
			projectName: "Unity Game Project",
			projectType: "Unity game",
			folder: "S4",
			mainImg: "mainImg.png",
			skills: [{ name: "Unity", type: "SW" }],
			imgs: [
				"S4img00.png",
				"S4img01.png",
				"S4img02.png",
				"S4img03.png",
				"S4img04.png",
				"S4img05.png",
			],
			descr: `is a 3D Role-Playing-Game that contains three scenes with the theme of forest, cargo ship, and city. The player is required to reach the location of three specific goals to win the game. The game development project is a group project and I am responsible to design the game logic, develop the character controller, assign tasks to other members, and oversee the project progress as a project manager.`,
		},
		{
			projectTitle: "智者行",
			projectName: "Mobile Application Development project",
			projectType: "Android application",
			folder: "S5",
			mainImg: "mainImg.png",
			skills: [{ name: "Android Studio", type: "SW" }],
			imgs: [
				"S5img00.png",
				"S5img01.png",
				"S5img02.png",
				"S5img03.png",
				"S5img04.png",
			],
			descr: (
				<p className='contentInfoText'>
					<b>智者行</b> is an Android application designed for elderly
					to facilitate their daily living. The application integrated
					easy access to useful features of the mobile phone into one
					single application. The application also provides a first
					aid guide and one-click seek help feature in an emergency
					situation.
				</p>
			),
		},
		{
			projectTitle: "日常猜與揀",
			projectName: "HONG KONG THEI, CARITAS JOINT PROJECT",
			projectType: "Application",
			folder: "S6",
			mainImg: "S6img002.png",
			skills: [{ name: "GameSalad", type: "SW" }],
			imgs: ["S6img002.png", "S6img00.png", "S6img01.png", "S6img02.png"],
			descr: (
				<p className='contentInfoText'>
					<b>日常猜與揀</b> is an application developed for Caritas
					Lok Ngai / Lok Yin Day Activity Center to facilitate their
					daily operation. The application provides a quiz game for
					the training of disabled and a voting feature.
				</p>
			),
		},
	];
	const fypContainerVariants = {
		init: {
			opacity: 0,
			x: 300,
		},
		anim: {
			opacity: 1,
			x: 0,
			transition: {
				when: "beforeChildren",
				duration: 0.5,
				type: "spring",
				stiffness: 100,
			},
		},
	};
	const childVariants = {
		init: {
			opacity: 0,
			y: 200,
		},
		anim: {
			opacity: 1,
			y: 0,
		},
	};
	return (
		<>
			<motion.div
				className='portfolioContentContainer'
				variants={window.$containerVariants}
				initial='init'
				animate='anim'
				exit='exit'>
				<h1 id='pageTitle' className='pageTitle'>
					Portfolio
				</h1>
				<motion.div
					className='fypContainer'
					variants={fypContainerVariants}>
					<div className='title-black fypTitle'>
						{fypInfo.projectName}
					</div>
					<div className='subTitle-black fypSubTitle'>
						{fypInfo.projectTitle}
					</div>
					<div className='fypContentContainer'>
						<div className='fypVideoContainer'>
							<div className='fypVideoIFrameWrapper'>
                                <Youtube height='100%' videoId="0yTI0TcRNnw"/>
							</div>
						</div>
						<div className='fypContent'>
							<ProjectInfo
								projectInfo={fypInfo}
								showDesc={true}
							/>
						</div>
					</div>
				</motion.div>
				<h2 id='pageTitle' className='pageTitle'>
					More projects
				</h2>
				<motion.div
					className='projectBlockBoard'
					variants={childVariants}
					transition={{
						delay: 1,
						duration: 1,
						type: "spring",
					}}>
					{projectInfos.map((projectInfo, index) => (
						<ProjectBlock
							key={`project-${index}`}
							projectInfo={projectInfo}
						/>
					))}
				</motion.div>
			</motion.div>
			<Footer />
		</>
	);
};

export default Portfolio;
